import * as React from "react";
import { GroupedList, IGroup } from "@fluentui/react/lib/GroupedList";
import { IColumn, DetailsRow } from "@fluentui/react/lib/DetailsList";
import {
  Selection,
  SelectionMode,
  SelectionZone,
} from "@fluentui/react/lib/Selection";
import { Toggle, IToggleStyles } from "@fluentui/react/lib/Toggle";
import { useBoolean, useConst } from "@fluentui/react-hooks";
import {
  createListItems,
  createGroups,
  IExampleItem,
  IExampleGroup,
} from "@fluentui/example-data";
import { Link, PrimaryButton, Stack } from "@fluentui/react";

import { initializeIcons } from "@fluentui/react/lib/Icons";
initializeIcons();

const toggleStyles: Partial<IToggleStyles> = { root: { marginBottom: "20px" } };
const groupCount = 12;
const groupDepth = 0;
const items = createListItems(54);
const columns = Object.keys(items[0])
  .slice(0, 3)
  .map(
    (key: string): IColumn => ({
      key: key,
      name: key,
      fieldName: key,
      minWidth: 300,
    })
  );

// const groups = createGroups(groupCount, groupDepth, 0, groupCount);

// console.log("GROUPS:", groups);
console.log("ITEMS:", items);

let counter = 0;
const grps: IExampleGroup[] = [];

for (let index = 1; index <= 12; index++) {
  if (index % 2) {
    grps.push({
      children: [],
      count: 3,
      isCollapsed: true,
      key: `group${index}`,
      level: 0,
      name: `group ${index}`,
      startIndex: counter,
    });
    counter = counter + 2;
  } else {
    grps.push({
      children: [],
      count: 6,
      isCollapsed: true,
      key: `group${index}`,
      level: 0,
      name: `group ${index}`,
      startIndex: counter,
    });
    counter = counter + 5;
  }

  if (index === 1) {
    grps[0].isCollapsed = false;
  }
}

export const App: React.FunctionComponent = () => {
  const [groups, setGrps] = React.useState(grps);
  const [allExpanded, setAllExpanded] = React.useState(true);
  const selection = useConst(() => {
    const s = new Selection();
    s.setItems(items, true);
    return s;
  });

  const onToogleExpand = () => {
    setAllExpanded(!allExpanded);
    const temp = grps.map((g) => {
      g.isCollapsed = !allExpanded;
      return g;
    });
    setGrps(temp);
  };

  const onRenderCell = (
    nestingDepth?: number,
    item?: IExampleItem,
    itemIndex?: number,
    group?: IGroup
  ): React.ReactNode => {
    return item ? (
      <div
        role="row"
        data-selection-index={itemIndex}
        style={{
          padding: "1rem 0rem 1rem 4rem",
          borderBottom: "1px solid white",
        }}
      >
        <Link role="cell" style={{ fontSize: "115%", fontWeight: "bold" }}>
          {item.name}
        </Link>
      </div>
    ) : null;
  };

  return (
    <div
      style={{
        margin: "3rem auto",
        height: "80vh",
        width: "60vw",
        background: "#d5d3d3",
        overflow: "auto",
        display: "flex",
        flexDirection: "column",
      }}
    >
      <PrimaryButton
        text="Expand / Collapse all"
        onClick={onToogleExpand}
        style={{ alignSelf: "end", padding: "1rem" }}
      />
      <GroupedList
        items={items}
        // eslint-disable-next-line react/jsx-no-bind
        onRenderCell={onRenderCell}
        selectionMode={SelectionMode.none}
        groups={groups}
      />
    </div>
  );
};
